
import { defineComponent, reactive, ref } from 'vue'
import { BranchDeptInfo, COM20200111_REQ_BODY, COM20200111_RES } from '@/shared/type/COM20200111.type';
import { PAGE_SIZE } from '@/shared/constants/common.const';
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue'
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';

export default defineComponent({
    name: 'com202001sc00',
    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const branchDeptListTable = ref<BranchDeptInfo[]>([])
        const branchDeptListData = ref<BranchDeptInfo[]>([])

        const header = [
            { columnName: this.$t('com202001sc00.column.no') },
            { columnName: this.$t('com202001sc00.column.view') },
            { columnName: this.$t('com202001sc00.column.branch_code') },
            { columnName: this.$t('com202001sc00.column.branch_dept_full_name'), sortable: false },
            { columnName: this.$t('com202001sc00.column.branch_short_name'), sortable: false },
            { columnName: this.$t('com202001sc00.column.branch_status_code'), sortable: false },
            { columnName: this.$t('com202001sc00.column.branch_type_code'), sortable: false }
        ]
        const input = reactive({
            branchName: "",
            branchCode: "",
            branchTypeCode: "",
            branchStatusCode: ""
        })
        return {
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            branchDeptListTable,
            header,
            input,
            branchDeptListData,
            isReset: false,
            breadcrumbs: [
                { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
                { value: 'com203002sc00.portal_management', keyPath: '', type: 'label' },
                { value: 'com202001sc00.branch_dept_list', keyPath: '', type: 'label' },
            ]
        }
    },
    created() {
        this.handleFetchBranchList()
    },
    computed: {
        branchStatusCode(): { key: string, value: string }[] {
            return this.$store.state.CommonCodeModule.commonCode.BranchStatusCode
        }
    },
    methods: {
        statusConverter(statusCode): string {
            switch (statusCode) {
                case '01: Active':
                    return 'success'
                case '02: Inactive':
                    return 'fail'
                default: '09: Closed'
                    return 'info'
            }
        },
        onMappingTableData(lists) {
            const { BranchTypeCode, BranchStatusCode } = this.$store.state.CommonCodeModule.commonCode;
            return lists.map((item, index) => ({
                No: index + 1,
                branchCode: item.branchCode,
                branchName: item.branchName,
                branchShortName: item.branchShortName,
                branchStatusCode: BranchStatusCode && item.branchStatusCode ? `${BranchStatusCode.find((type) => type.key === item.branchStatusCode)?.key}: ${BranchStatusCode.find((type) => type.key === item.branchStatusCode)?.value}` : '',
                branchTypeCode: BranchTypeCode && item.branchTypeCode? `${BranchTypeCode.find((type) => type.key === item.branchTypeCode)?.key}: ${BranchTypeCode.find((type) => type.key === item.branchTypeCode)?.value}` : '',
            }));
        },
        handleClickRegister() {
            this.$router.push({
                path: '/COM202001SC01',
                query: {
                    type: 'register'
                }
            })
        },
        handleClickRow(index, rowData: BranchDeptInfo) {
            this.$router.push({
                path: '/COM202001SC01',
                query: {
                    type: 'edit',
                    branchCode: rowData.branchCode
                }
            })
        },
        // Search Key 
        handleClickSearch() {
            this.handleFetchBranchList();
        },
        // Clear Search 
        async handleClickRefresh() {
            this.isReset = true;
            this.currentPage = 1;
            this.input.branchCode = "",
            this.input.branchName = "",
            this.input.branchTypeCode = ""
            this.input.branchStatusCode = ""
            this.branchDeptListData = this.branchDeptListTable
            await this.handleFetchBranchList();
        },
        // Type Search List 
        async handleFetchBranchList(mode?: string) {
            const trCode = 'COM20200111'
            const bMock = false
            const body: COM20200111_REQ_BODY = {
                branchCode: this.input.branchCode,
                branchName: this.input.branchName,
                branchStatusCode: this.input.branchStatusCode,
                branchTypeCode: this.input.branchTypeCode,
                pageNumber: mode == 'search' ? 0 : this.currentPage,
                pageSize: this.pageSize
            }
            if (mode === 'search') {
                this.isReset = true;
                this.currentPage = 1
            }
            const res = await this.$request.getRequest(trCode, body, bMock) as COM20200111_RES

            if (res.header.result) {
                this.branchDeptListTable = JSON.parse(JSON.stringify(this.onMappingTableData(res?.body?.list)))
                this.totalItems = res.body.totalCount
            }
        },
        handlePagination(pageNumber: number) {
            this.isReset = false;
            this.currentPage = pageNumber
            this.handleFetchBranchList()
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        }
    }
})
